}

.header__container {
  position: relative;
  display: grid;
  margin: 0 auto;
  padding: 0;
  grid-template-columns: 1fr 60px 60px 60px;
  grid-template-rows: 60px;
  grid-template-areas: "logo user basket toggle" "nav nav nav nav";
}

.header__logo-grid {
  grid-area: logo;
}

.header__nav-grid {
  grid-area: nav;
}

.header__user-grid {
  grid-area: user;
}

.header__basket-grid {
  grid-area: basket;
}

@media (min-width: 768px) {
