.header__overflow-hidden {
  overflow: hidden;
}

.header-nav--nojs {
  display: block;
}

.header-nav__list {
  display: none;
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}

.header-nav__item {
  border-bottom: 1px solid #f0ebff;
}
.header-nav__item:first-child {
  border-top: 1px solid #f0ebff;
}

.header-nav__link {
  display: block;
  padding: 18px 28px;
  font-size: 12px;
  font-weight: 500;
  text-align: center;
  line-height: 16px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
.header-nav__link:active {
  background-color: #f0ebff;
}

.header-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  grid-area: toggle;
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 30px;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
.header-nav__toggle:active {
  background-color: #f0ebff;
}

.header-nav--closed .header-nav__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 22px;
  width: 16px;
  height: 1.5px;
  background-color: #231f20;
  box-shadow: 0 5px 0 0 #231f20, 0 10px 0 0 #231f20;
}

@media (max-width: 767px) {
  .header-nav--closed .header-nav__list {
    display: none;
  }

  .header-nav--nojs .header-nav__list {
    display: block;
  }

  .header-nav--nojs .header-nav__toggle {
    display: none;
  }
}
.header-nav--opened .header-nav__list {
  display: block;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #ffffff;
}

.header-nav--opened .header-nav__toggle {
  display: block;
}
.header-nav--opened .header-nav__toggle::before, .header-nav--opened .header-nav__toggle::after {
  content: "";
  position: absolute;
  top: 28px;
  right: 21px;
  width: 18px;
  height: 2px;
  background-color: #000000;
}
.header-nav--opened .header-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header-nav--opened .header-nav__toggle::after {
  transform: rotate(-45deg);
}

@media (min-width: 768px) {
  .header-nav {
    display: flex;
  }

  .header-nav__list {
    display: flex;
  }

  .header-nav__item {
    display: inline-block;
    border: 0;
  }
  .header-nav__item:first-child {
    border: 0;
  }

  .header-nav__link {
    padding: 32px 20px;
  }
  .header-nav__link--active {
    position: relative;
    color: #7859cf;
  }
  .header-nav__link--active::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    border-bottom: 2px solid #7859cf;
  }
  .header-nav__link:hover, .header-nav__link:focus {
    color: #7859cf;
  }

  .header-nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
