  margin-bottom: 17px;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.02em;
  color: #7859cf;
}

.footer-shop__list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  letter-spacing: 0.02em;
}

.footer-shop__item {
  margin-bottom: 16px;
}
.footer-shop__item:last-child {
  margin-bottom: 0;
}

.footer-shop__link {
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  text-transform: uppercase;
}
.footer-shop__link:hover, .footer-shop__link:focus {
  color: #9070ec;
}
.footer-shop__link:active {
  color: #593cac;
}

@media (min-width: 768px) {
  .footer-shop {
    padding: 0;
    border-bottom: 0;
  }

  .footer-shop__container {
    width: 100%;
    padding: 0;
  }
}
