.footer-contacts {
  padding: 20px 0;
  border-bottom: 1px solid #f0ebff;
}

.footer-contacts__container {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-contacts__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.02em;
  color: #7859cf;
}

.footer-contacts__address {
  position: relative;
  padding-left: 19px;
  padding-bottom: 16px;
  font-style: normal;
}
.footer-contacts__address::before {
  content: url("../../img/footer/address.svg");
  position: absolute;
  left: 0;
  top: 2px;
}

.footer-contacts__phone {
  position: relative;
  padding-left: 19px;
}
.footer-contacts__phone::before {
  content: url("../../img/footer/phone.svg");
  position: absolute;
  left: 0;
  top: 1px;
}

@media (min-width: 768px) {
  .footer-contacts {
    padding: 0;
    border-bottom: 0;
  }

  .footer-contacts__container {
    padding: 0;
    width: 100%;
  }
}
.footer-academy {
  margin: 0 auto;
  padding-top: 20px;
}

.footer-academy__container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin: 0 auto;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #bdbdbd;
}

.footer-academy__link {
  display: flex;
  margin-left: 3px;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #bdbdbd;
}
.footer-academy__link:hover, .footer-academy__link:focus {
  color: #7859cf;
}
.footer-academy__link:active {
  color: #593cac;
}

.footer-academy__link:hover .footer-academy__icon,
.footer-academy__link:focus .footer-academy__icon {
  fill: #7859cf;
}

.footer-academy__link:active .footer-academy__icon {
  fill: #593cac;
}

.footer-academy__icon {
  margin-left: 4px;
  fill: #bdbdbd;
}

@media (min-width: 768px) {
  .footer-academy {
    padding-top: 37px;
  }
}
@media (min-width: 1440px) {
  .footer-academy {
    padding-top: 27px;
  }
}